}


/* ITEM SCREEN CLASSES */
.item_prompt {
  grid-column: 1;
}
.item_input {
  grid-column: 2;
  margin: auto;
}

.promptInputDiv{

}
.item_form_container{
  
}

.itemCard{
  position: relative;
}

.moveUpHoverBtn, .moveDownHoverBtn, .deleteHoverBtn{
  display: none;
}
.moveUpHoverBtn{
    -webkit-transition: 1s;
    transition: 1s;
    position: absolute;
    left: 0; /*-25% */ 
    top: 32%;
    background-color: #1222ed;  
    -webkit-animation: slide 0.5s backwards;
    -webkit-animation-delay: 2s;
    -webkit-animation: slide3 0.5s forwards;
            animation: slide3 0.5s forwards;
}
.moveDownHoverBtn{
  -webkit-transition: 1s;
  transition: 1s;
  position: absolute;
  left: 0;  /* -17.5 */
  top: 32%;
  background-color: #8410ef;  
  -webkit-animation: slide 0.5s backwards;
  -webkit-animation-delay: 2s;
  -webkit-animation: slide2 0.5s forwards;
          animation: slide2 0.5s forwards;
}
.deleteHoverBtn{
  -webkit-transition: 1s;
  transition: 1s;
  position: absolute;
  left: 0; 
  color: black;
  top: 32%;
  background-color: #d35400;  
  -webkit-animation: slide 0.5s backwards;
  -webkit-animation-delay: 2s;
  -webkit-animation: slide 0.5s forwards;
          animation: slide 0.5s forwards;
}

.listHeader{
  
}
.deleteListBtn{
  float: right;
}

/* Animation for buttons */
@-webkit-keyframes slide {
  100% { left: 0; }
}

@keyframes slide {
  100% { left: -10%; }
}
@-webkit-keyframes slide2 {
  100% { left: -17.5%; }
}
@keyframes slide2 {
  100% { left: -17.5%; }
}
@-webkit-keyframes slide3 {
  100% { left: -25%; }
}
@keyframes slide3 {
  100% { left: -25%; }
}
:root {
  /* 
      FIRST WE'LL DEFINE OUR SWATCHES, i.e. THE COMPLEMENTARY
      COLORS THAT WE'LL USE TOGETHER IN MULTIPLE PLACES THAT
      TOGETHER WILL MAKE UP A GIVEN THEME
  */
  --swatch-foundation:    #EEEEDD; /* USED FOR BODY BACKGROUND */
  --swatch-primary:       #E6E6E6; /* DOMINANT COLOR, USED FOR MOST BACKGROUNDS */
  --swatch-complement:    #E1E4CB; /* USED IN FORGROUND OF PRIMARY FOR TEXT AND BORDERS */
  --swatch_contrast:      #111111; /* USED IN MOUSE-OVER SELECTION OF LARGE ELEMENTS */
  --swatch-accent:        #669966; /* USED IN MOUSE-OVER SELECTION OF TEXT AND BORDERS */    
  --my-font-family:          "Robaaaoto";
  --bounceEasing: cubic-bezier(0.51, 0.92, 0.24, 1.15);
}

body {
  background-color: #EEEEDD;
  background-color: var(--swatch-foundation);
  font-family: 'Lexend Exa';
}

div.banner {
  background-image: -webkit-gradient(linear,         
  left top, left bottom,         
  from(#b8b808), to(#636723));
  background-image: linear-gradient(to bottom,         
  #b8b808, #636723);
  color: white;
  font-size: 48pt;
  border-color: black;
  border-width: 2px;
  border-style: solid;
  border-radius: 10px;
  padding-left: 10%;
  padding-top: 5%;
  padding-bottom: 5%;
}

.home_new_list_button {
  font-size: 32pt;
}
.home_new_list_button:hover {
  box-shadow: 1px 2px;
}

.card{
  border: 5px ridge #1C6EA4;
  background-color: white;
}

.addACardBtn {
	box-shadow: 0px 0px 0px 0px #3dc21b;
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #44c767), to(#5cbf2a));
	background:linear-gradient(to bottom, #44c767 5%, #5cbf2a 100%);
	background-color:#44c767;
	border-radius:28px;
	border:1px solid #18ab29;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:17px;
	padding:16px 31px;
	text-decoration:none;
	text-shadow:0px 1px 0px #2f6627;
}
.addACardBtn:hover {
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #5cbf2a), to(#44c767));
	background:linear-gradient(to bottom, #5cbf2a 5%, #44c767 100%);
	background-color:#5cbf2a;
}
.addACardBtn:active {
	position:relative;
	top:1px;
}
.card-description, .card-assignedTo{
  font-size: 120%;
}

.card-due_Date{
  font-size: 200%;
}
.card-completed, .card-not-completed{
  font-size: 200%;
}

.card-completed {
  color: green;
}
.card-not-completed {
  color: red;
}

.card-headers{
  background-color: #669966;
  font-size: 200%;
}

.todo-lists{
  background-color: #EEEEDD;
  background-color: var(--swatch-foundation);
}

.item_form_container{
  background-color: white;
  padding: 50px;
}
.cancel_button,.cancel_button:hover, .deleteListBtn, .deleteListBtn:hover{
  background-color: #dc235d;
}
.cancel_button:hover, .deleteListBtn:hover{
  box-shadow: 0 0 15px #d35400;
  text-shadow: 0 0 15px #d35400;
}
.itemCard:hover{
  box-shadow: 0 0 15px #00d370;
}
